<template>
  <v-container>
    <v-layout>
      <v-flex>
        <v-flex style="margin-bottom:15rem" v-for="n in 3" :key="n">
          <v-hover v-if="n != 2">
            <v-card slot-scope="{ hover }" flat :class="`${hover ? 'transparent' : 'transparent'}`">
              <div>
                <!-- color="transparent" -->
                <div class="d-flex">
                  <v-layout :column="$vuetify.breakpoint.xsOnly">
                    <v-flex
                      :class="` ${hover ? 'bounce' : ''}`"
                      style="margin-right:auto;margin-left:auto;font-size:12rem"
                    >{{emoji[n-1]}}</v-flex>
                    <div style="padding:3rem">
                      <p
                        :id="`element${n}`"
                        style="margin-bottom:3.5rem;font-weight:bold;text-align:end"
                      >
                        <a
                          style="margin-right:2rem"
                          :href="`#title-${n}`"
                          class="underlined underlined--reverse"
                        >{{title[n-1]}}</a>
                      </p>
                      <p style="text-indent:4em">
                        <a
                          :href="`#content-${n}`"
                          class="underlined underlined--thin"
                        >{{content[n-1]}}</a>
                      </p>
                    </div>
                  </v-layout>
                </div>
              </div>
            </v-card>
          </v-hover>
          <v-hover v-if="n == 2">
            <v-card slot-scope="{ hover }" flat :class="` ${hover ? 'orange' : 'transparent'}`">
              <div>
                <!-- color="transparent" -->
                <div class="d-flex">
                  <v-layout :column="$vuetify.breakpoint.xsOnly">
                    <div style="padding:2rem">
                      <p :id="`element${n}`" style="margin-bottom:3.5rem; font-weight:bold">
                        <a :href="`#title-${n}`" class="underlined underlined--title">{{title[n-1]}}</a>
                      </p>
                      <p style="text-indent:4em">
                        <a
                          :href="`#content-${n}`"
                          class="underlined underlined--thin"
                        >{{content[n-1]}}</a>
                      </p>
                    </div>
                    <v-spacer></v-spacer>
                    <div
                      :class="` ${hover ? 'bounce' : ''}`"
                      style="margin-right:auto;margin-left:auto;font-size:12rem;max-width:180px;"
                    >{{emoji[n-1]}}</div>
                  </v-layout>
                </div>
              </div>
            </v-card>
          </v-hover>
        </v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</template>



<script>
export default {
  name: "ServiceCard",

  data: () => ({
    emoji: ["", "", ""],
    title: [
      "",
      "",
      ""
    ],
    content: [
      "",
      "",
      ""
    ]
  })
};
</script>



<style lang="scss" scoped>
//emoji

.bounce {
  animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  -moz-animation: bounce 2s infinite;
  -o-animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
  }
}

@-moz-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
  }
}

@-o-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -o-transform: translateY(0);
  }
  40% {
    -o-transform: translateY(-30px);
  }
  60% {
    -o-transform: translateY(-15px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

// css
// Now that is interesting
.underlined {
  color: black;
  flex: 1;
  font-size: 1.8em;
  line-height: 1.7;
  text-decoration: none;
  background-image: linear-gradient(to right, orange 0, orange 100%);
  background-position: 0 1.2em;
  background-size: 0 100%;
  background-repeat: no-repeat;
  transition: background 0.5s;
  &:hover {
    background-size: 100% 100%;
  }
  &--thin {
    background-image: linear-gradient(to right, black 0, black 100%);
  }
  &--thick {
    background-position: 0 -0.1em;
  }
  &--offset {
    background-position: 0 0.2em;
    //didn't find another solution than mask the underline shape by a box shadow with the same color than the bg
    box-shadow: inset 0 -0.5em 0 0 white;
  }
  &--gradient {
    background-position: 0 -0.1em;
    background-image: linear-gradient(to right, orange 0, lightgreen 100%);
  }
  &--reverse {
    background-position: 100% -0.1em;
    transition: background 1s; //yep, that's a long link
    background-image: linear-gradient(to right, orange 0, orange 100%);
  }
  &--title {
    background-position: 100% -0.1em;
    transition: background 1s; //yep, that's a long link
    background-image: linear-gradient(
      to right,
      rgb(133, 180, 194) 0%,
      rgb(133, 180, 194) 100%
    );
  }
}
</style>